        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-tianditu</span> <span class="hljs-attr">map-style</span>=<span class="hljs-string">"cia_c"</span> <span class="hljs-attr">:token</span>=<span class="hljs-string">"tk"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-tianditu</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tool"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>动画部件<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"animation"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>时间轴部件<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"timeline"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>基础图层拾取器<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"baseLayerPicker"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>全屏按钮<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"fullscreenButton"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>信息提示框<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"infoBox"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">animation</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">timeline</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">baseLayerPicker</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">fullscreenButton</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">infoBox</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">fullscreenElement</span>: <span class="hljs-variable language_">document</span>.<span class="hljs-property">body</span>,
        <span class="hljs-attr">tk</span>: <span class="hljs-string">'436ce7e50d27eede2f2929307e6b33c0'</span>,
        <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'./statics/SuperMapCesium/Cesium.js'</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">vcViewer</span>.<span class="hljs-property">createPromise</span>.<span class="hljs-title function_">then</span>(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer }</span>) =&gt;</span> {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'viewer is loaded.'</span>)
      })
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
        viewer.<span class="hljs-property">scene</span>.<span class="hljs-property">globe</span>.<span class="hljs-property">depthTestAgainstTerrain</span> = <span class="hljs-literal">true</span>
        viewer.<span class="hljs-property">scene</span>.<span class="hljs-property">globe</span>.<span class="hljs-property">enableLighting</span> = <span class="hljs-literal">true</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">fullscreenElement</span> = <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">viewerContainer</span>
        viewer.<span class="hljs-property">entities</span>.<span class="hljs-title function_">add</span>({
          <span class="hljs-attr">id</span>: <span class="hljs-string">'成都欢迎你'</span>,
          <span class="hljs-attr">position</span>: <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Cartesian3</span>.<span class="hljs-title function_">fromDegrees</span>(<span class="hljs-number">104.06</span>, <span class="hljs-number">30.67</span>, <span class="hljs-number">100</span>),
          <span class="hljs-attr">billboard</span>: <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">BillboardGraphics</span>({
            <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>,
            <span class="hljs-attr">scale</span>: <span class="hljs-number">0.1</span>
          }),
          <span class="hljs-attr">label</span>: <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">LabelGraphics</span>({
            <span class="hljs-attr">text</span>: <span class="hljs-string">'Hello Cesium'</span>,
            <span class="hljs-attr">font</span>: <span class="hljs-string">'24px sans-serif'</span>,
            <span class="hljs-attr">horizontalOrigin</span>: <span class="hljs-number">1</span>,
            <span class="hljs-attr">outlineColor</span>: <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Color</span>(<span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">1</span>),
            <span class="hljs-attr">outlineWidth</span>: <span class="hljs-number">2</span>,
            <span class="hljs-attr">pixelOffset</span>: <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian2</span>(<span class="hljs-number">17</span>, -<span class="hljs-number">5</span>),
            <span class="hljs-attr">style</span>: <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">LabelStyle</span>.<span class="hljs-property">FILL</span>
          })
        })
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<hr>
<ul>
<li>参考超图官方文档： <a href="http://support.supermap.com.cn:8090/webgl/">SuperMap iClient 3D for WebGL</a></li>
</ul>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          animation: true,
          timeline: true,
          baseLayerPicker: false,
          fullscreenButton: true,
          infoBox: true,
          fullscreenElement: document.body,
          tk: '436ce7e50d27eede2f2929307e6b33c0',
          cesiumPath: './statics/SuperMapCesium/Cesium.js'
        }
      },
      mounted() {
        this.$refs.vcViewer.createPromise.then(({ Cesium, viewer }) => {
          console.log('viewer is loaded.')
        })
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.scene.globe.enableLighting = true
          this.fullscreenElement = this.$refs.viewerContainer
          viewer.entities.add({
            id: '成都欢迎你',
            position: Cesium.Cartesian3.fromDegrees(104.06, 30.67, 100),
            billboard: new Cesium.BillboardGraphics({
              image: 'https://zouyaoji.top/vue-cesium/favicon.png',
              scale: 0.1
            }),
            label: new Cesium.LabelGraphics({
              text: 'Hello Cesium',
              font: '24px sans-serif',
              horizontalOrigin: 1,
              outlineColor: new Cesium.Color(0, 0, 0, 1),
              outlineWidth: 2,
              pixelOffset: new Cesium.Cartesian2(17, -5),
              style: Cesium.LabelStyle.FILL
            })
          })
        }
      }
    }
  </script>